.slogan {
  width: 280px;
  margin: 0 auto;
  position: relative;
  margin-bottom: 25px;
  padding-top: 27px;

  &::before {
    position: absolute;
    content: "";
    background:
      linear-gradient(
        hsl(97 53% 47% / 85%),
        hsl(97 53% 47% / 85%)
      ),
      url("../img/background-wrapper.jpg");
    background-repeat: no-repeat;
    background-size: 320px 302px;
    width: 320px;
    height: 302px;
    z-index: -1;
    bottom: 180px;
    left: -20px;

    @media (min-width: $tablet-width) {
      background: none;
    }

    @media (min-width: $desktop-width) {
      position: absolute;
      content: "";
      background:
        linear-gradient(
        hsl(97 53% 47% / 85%),
        hsl(97 53% 47% / 85%)
      ),
        url("../img/background-wrapper-desktop.jpg");
      background-repeat: no-repeat;
      background-size: 720px 694px;
      width: 720px;
      height: 694px;
      z-index: -1;
      top: -96px;
      left: 50%;
    }
  }

  @media (min-width: $tablet-width) {
    width: 768px;
    margin: 0 30px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
    margin: 0 auto;
    position: relative;
    padding-top: 111px;
  }
}

.slogan__heading-main {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  color: rgba(255, 255, 255);
  font-size: 36px;
  line-height: 36px;
  text-align: center;

  @media (min-width: $tablet-width) {
    width: 416px;
    color: rgba(0, 0, 0, 1);
    font-size: 60px;
    line-height: 60px;
    text-align: start;
    margin-left: 87px;
    margin-bottom: 41px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 137px;
  }
}

.slogan__heading {
  margin: 25px 0 29px 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: rgb(252, 252, 252);
  text-align: center;

  @media (min-width: $tablet-width) {
    color: rgba(0, 0, 0, 1);
    font-size: 20px;
    line-height: 20px;
    text-align: start;
    margin-left: 87px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 137px;
  }
}

.slogan__image {
  @media (min-width: $desktop-width) {
    position: absolute;
    top: 20px;
    left: 39%;
  }
}
